import elements from '../views/Base'
import * as board from '../views/BoardView';

export let snakePositions = [];
export const resetSnakePosition = (boardCols, boardRows) => {
    snakePositions = [];
    for (let i = 0; i < 4; i++) {
        let c = (boardCols / 2) + i;
        let r = (boardRows / 2);
        snakePositions.push({
            x: c,
            y: r
        });
    }
}


export const moveSnakePosition = (direction) => {
    switch (direction) {
        case 'Up':
            break;
        case 'Down':
            break;
        case 'Left':
            break;
        case 'Right':
            break;
        default:
            break;
    }
};

export const showSnake = () => {
    snakePositions.forEach = (element) => {
        debugger;
        elements.board.getElementById(`${element.r, element.c}`).style.backgroundColor = "lightblue";
    };
}


